.wrapper {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	left: 0;
	right: 0;
	bottom: 120px;
}

.movieName,
.infoWrapper,
.wrapper {
	transition: all 0.3s;
}

.movieName {
	text-transform: uppercase;
	font-size: 40px;
	color: #fff;
	margin: 0;
}

.infoWrapper {
	display: flex;
	color: #fff;
	font-size: 18px;
	margin-top: 10px;
}

.genre {
	cursor: pointer;
	margin-right: 10px;
	transition: all 0.3s;
}

.genre:hover {
	color: #3bb4e0;
}

.time {
	margin-left: 10px;
}

.rating {
	display: flex;
	margin-top: 20px;
}

@media (min-width: 1100px) {
	.wrapper {
		display: block;
		left: 50px;
		bottom: 50px;
	}
}

@media (min-width: 1300px) {
	.movieName {
		font-size: 50px;
	}
	.infoWrapper {
		font-size: 24px;
	}
	.genre {
		margin-right: 20px;
	}
	.time {
		margin-left: 20px;
	}
	.wrapper {
		left: 70px;
	}
}
